  .post{
    display: flex;
    flex-wrap: wrap;
    margin-top: 15px;
    width: 90%;
    min-height: 80px;
 text-align: left;
 background-color:#91CB40;
 padding-left: 50px;
 font-size: large;
 border-radius: 81px 81px 81px 81px;
-webkit-border-radius: 81px 81px 81px 81px;
-moz-border-radius: 81px 81px 81px 81px;
      }
      .post p{
        margin-bottom: 5px;
        width: 380px;
        height: auto;
        text-align: justify;
        overflow-wrap: break-word;
        
      }
    .post img{
      width: 50px;
      height: 50px;
      margin: 5px 10px 5px 0px;
    }
    .button img{
      width: 10px;
      height: 10px;
    }
    .buttons{
      background-color: transparent;
      width: 400px;
      height: 30px;
    }
    
    button{
      margin: 0px 5px 0px 5px;
      font-size: small;
    }
    .like {
      width: 50px;
      height: 30px;
      background-color: green;
      float: right;
    }
    .dislike{
      width: 40px;
      height: 30px;
      background-color: red;
      float: right;
    }
    .comment{
      width: 100px;
      height: 30px;
      background-color: whitesmoke;
      float: left;    
    }
    .repost {
      background-color: whitesmoke;
      width: 80px;
      height: 30px;
      float: left;
    }